import Sortable from 'sortablejs';
import { deep_copy, maybe_call, generateUUID } from 'utilities/object';
import { hasSharedParent } from 'utilities/dom';

export default {
	props: {
		value: Array,
		filters: Object,
		disabled: {
			type: Boolean,
			default: false
		},
		maxRules: {
			type: Number,
			required: false,
			default: 0
		},
		preview: {
			type: Boolean,
			required: false,
			default: true
		},
		context: {
			type: Object,
			required: false
		}
	},

	data() {
		return {
			adding: false,
			resetting: false,
			editing: this.copyValue()
		}
	},

	computed: {
		canAddRule() {
			if ( this.disabled )
				return false;
			return ! this.maxRules || (this.editing.length < this.maxRules);
		}
	},

	watch: {
		value() {
			this.resetting = true;
			this.editing = this.copyValue();
		},

		editing: {
			handler() {
				if ( ! this.resetting && ! this.disabled)
					this.$emit('input', this.editing)
				this.resetting = false;
			},
			deep: true
		}
	},

	mounted() {
		this.sortable = Sortable.create(this.$refs.list, {
			draggable: 'section',
			filter: 'button,.sortable-ignore',
			swapThreshold: 0.33,
			group: {
				name: 'ffz-filter-editor',
				put: (to, from, dragged) => {
					if ( ! this.canAddRule )
						return false;

					// Avoid moving an element into its child list.
					if ( dragged && dragged.contains && dragged.contains(to.el) )
						return false;

					// Check to see if we have a common ancester for the two
					// draggables.
					if ( ! hasSharedParent(to.el, from.el, '.ffz--rule-list') )
						return false;

					return true;
				}
			},

			setData: (data, el) => {
				const rule = this.getRule(el.dataset.id);
				if ( rule ) {
					data.setData('JSON', JSON.stringify(rule));
				}
			},

			onAdd: event => {
				if ( this.disabled ) {
					event.preventDefault();
					return;
				}

				if ( ! this.canAddRule ) {
					event.preventDefault();
					return;
				}

				let rule;
				try {
					rule = JSON.parse(event.originalEvent.dataTransfer.getData('JSON'));
				} catch(err) {
					event.preventDefault();
					return;
				}

				this.editing.splice(event.newDraggableIndex, 0, rule);
			},

			onRemove: event => {
				if ( this.disabled ) {
					event.preventDefault();
					return;
				}

				let rule;
				try {
					rule = JSON.parse(event.originalEvent.dataTransfer.getData('JSON'));
				} catch(err) {
					event.preventDefault();
					return;
				}

				this.deleteRule(rule.id);
			},

			onUpdate: event => {
				if ( this.disabled )
					return;

				if ( event.newIndex === event.oldIndex )
					return;

				this.editing.splice(event.newIndex, 0, ...this.editing.splice(event.oldIndex, 1));
			}
		});
	},

	beforeDestroy() {
		if ( this.sortable ) {
			this.sortable.destroy();
			this.sortable = null;
		}
	},

	methods: {
		getRule(id, start) {
			if ( ! start )
				start = this.editing;

			if ( ! Array.isArray(start) )
				return null;

			for(let i=0; i < start.length; i++) {
				const rule = start[i];
				if ( ! rule )
					continue;
				else if ( rule.id === id )
					return rule;

				const type = this.filters[rule.type];
				if ( type && type.childRules ) {
					const out = this.getRule(id, rule.data);
					if ( out )
						return out;
				}
			}

			return null;
		},

		addRule() {
			if ( this.disabled )
				return;

			this.adding = false;

			const key = this.$refs.add_box.value,
				type = this.filters[key];

			if ( ! key )
				return;

			const out = {
				id: generateUUID(),
				type: key,
				data: maybe_call(type.default, type)
			};

			this.editing.push(out);
		},

		updateRule(id, data) {
			if ( this.disabled )
				return;

			for(let i=0; i < this.editing.length; i++) {
				if ( this.editing[i].id === id ) {
					this.editing[i] = Object.assign(this.editing[i], data);
					return;
				}
			}
		},

		deleteRule(id) {
			if ( this.disabled )
				return;

			for(let i=0; i < this.editing.length; i++) {
				if ( this.editing[i].id === id ) {
					this.editing.splice(i, 1);
					return;
				}
			}
		},

		copyValue() {
			if ( ! Array.isArray(this.value) )
				return [];

			return deep_copy(this.value).map(rule => {
				if ( ! rule.id )
					rule.id = generateUUID();

				return rule;
			});
		}
	}
}

