export default {
	props: ['item', 'context', 'filter'],

	data() {
		return {
			ready: this.item.isReady(),
			reload: this.item.isReloadRequired(),
			unlisted: [],
			filter_enabled: false,
			sort_by: 0,
			unlisted_open: false
		}
	},

	computed: {
		visible_unlisted() {
			return this.visible_addons.length !== this.sorted_addons.length
		},

		visible_addons() {
			return this.sorted_addons.filter(addon => this.shouldShow(addon));
		},

		listed_addons() {
			return this.sorted_addons.filter(addon => ! addon.unlisted)
		},

		sorted_addons() {
			const addons = this.item.getAddons();

			addons.sort((a, b) => {
				if ( this.sort_by === 1 ) {
					if ( a.updated > b.updated ) return -1;
					if ( b.updated > a.updated ) return 1;

				} else if ( this.sort_by === 2 ) {
					if ( a.created > b.created ) return -1;
					if ( b.created > a.created ) return 1;
				}

				if ( a.sort < b.sort ) return -1;
				if ( b.sort < a.sort ) return 1;

				const a_n = (a.name ?? a.id).toLowerCase(),
					b_n = (b.name ?? b.id).toLowerCase();

				if ( a_n < b_n ) return -1;
				if ( b_n < a_n ) return 1;
				return 0;
			});

			return addons;
		}
	},

	created() {
		this.item.on(':ready', this.onReady, this);
		this.item.on(':added', this.onAdded, this);
		this.item.on(':reload-required', this.onReload, this);
	},

	destroyed() {
		this.item.off(':ready', this.onReady, this);
		this.item.off(':added', this.onAdded, this);
		this.item.off(':reload-required', this.onReload, this);
	},

	methods: {
		addUnlisted() {
			let value = this.$refs.unlisted.value;
			if ( value )
				value = value.trim().toLowerCase();

			if ( value && value.length )
				for(const addon of this.item.getAddons())
					if ( addon.unlisted && (addon.id === value || (addon.short_name && addon.short_name.toLowerCase() === value) || (addon.name && addon.name.toLowerCase() === value)) )
						this.unlisted.push(addon.id);

			this.$refs.unlisted.value = '';
			this.closeUnlisted();
		},

		closeUnlisted() {
			this.unlisted_open = false;
		},

		toggleUnlisted() {
			this.unlisted_open = ! this.unlisted_open;
		},

		shouldShow(addon) {
			// If an add-on is unlisted, don't list it.
			const enabled = this.item.isAddonEnabled(addon.id);
			if ( addon.unlisted && ! enabled && ! this.unlisted.includes(addon.id) )
				return false;

			if ( this.filter_enabled && ! enabled )
				return false;

			if ( this.filter ) {
				if ( this.filter.query ) {
					if ( ! addon.search_terms || ! addon.search_terms.includes(this.filter.query) )
						return false;
				}
			}

			return true;
		},

		onAdded() {
			this.$forceUpdate();
		},

		onReady() {
			this.ready = true;
		},

		onReload() {
			this.reload = true;
		},

		navigate(...args) {
			this.$emit('navigate', ...args);
		},

		changeItem(...args) {
			this.$emit('change-item', ...args);
		}
	}
}
