import SettingMixin from '../setting-mixin';

export default {
	mixins: [SettingMixin],
	props: ['item', 'context'],

	computed: {
		type() {
			return this.item.type || 'text';
		},

		placeholder() {
			return this.item.placeholder || this.default_value;
		}
	},

	methods: {
		onChange() {
			const value = this.$refs.control.value;
			if ( value != null )
				this.set(value);
		}
	}
}
