export default {
	props: ['act', 'process-color', 'tooltip', 'pad', 'renderers'],

	computed: {
		color() {
			return this['processColor'](this.act.appearance.color);
		},

		renderer() {
			return this.renderers[this.act.appearance.type]
		}
	}
}

