export default {
	props: ['item', 'context'],

	data() {
		const key = this.item.key;

		return {
			key,
			loading: true,
			md: null
		}
	},

	mounted() {
		this.load();
	},

	methods: {
		async load() {
			this.md = (await import(/* webpackChunkName: 'menu-md' */ `../${this.key}.md`)).default;
			this.loading = false;
		}
	}
}
