import SettingMixin from '../setting-mixin';
import {deep_copy} from 'utilities/object';

let last_id = 0;

export default {
	mixins: [SettingMixin],
	props: ['item', 'context'],

	data() {
		return {
			default_reason: {
				text: ''
			}
		}
	},

	computed: {
		empty() {
			return ! this.val.length || this.val.length === 1 && this.hasInheritance;
		},

		reasons() {
			const out = [];
			if ( Array.isArray(this.val) )
				for(const reason of this.val)
					if ( reason.t !== 'inherit' )
						out.push(reason);

			return out;
		},

		hasInheritance() {
			for(const val of this.val)
				if ( val.t === 'inherit' )
					return true;

			return false;
		},

		val() {
			if ( ! this.has_value )
				return [];

			return this.value.map(x => {
				x.id = x.id || `${Date.now()}-${Math.random()}-${last_id++}`;
				return x;
			})
		}
	},

	methods: {
		populate() {
			this.set(deep_copy(this.default_value));
		},

		new_reason(reason) {
			if ( ! reason )
				return;

			delete reason.i18n;

			const vals = Array.from(this.val);
			vals.push({v: reason});
			this.set(deep_copy(vals));
		},

		remove(val) {
			const vals = Array.from(this.val),
				idx = vals.indexOf(val);

			if ( idx !== -1 ) {
				vals.splice(idx, 1);
				if ( vals.length )
					this.set(deep_copy(vals));
				else
					this.clear();
			}
		},

		save(val, new_val) {
			delete new_val.i18n;
			val.v = new_val;

			this.set(deep_copy(this.val));
		}
	}
}

