import displace from 'displacejs';
import { getDialogNextZ } from 'src/utilities/dialog';

const VALID_FLAGS = [
	'modified'
];

export default {
	data() {
		const out = this.$vnode.data;

		out.z = getDialogNextZ();

		return out;
	},

	computed: {
		filter() {
			let query = this.query.toLowerCase();

			let flags = new Set;
			query = query.replace(/(?<=^|\s)@(\S+)(?:\s+|$)/g, (match, flag, index) => {
				if ( VALID_FLAGS.includes(flag) ) {
					flags.add(flag);
					return '';
				}

				return match;
			});

			query = query.trim();
			if ( ! query.length )
				query = null;

			if ( ! flags.size )
				flags = null;

			if ( ! query && ! flags )
				return null;

			return {
				flags,
				query
			}
		}
	},

	watch: {
		maximized() {
			this.updateDrag();
		}
	},

	created() {
		this.context.context._add_user();
	},

	destroyed() {
		this.context.context._remove_user();
	},

	mounted() {
		this.updateDrag();

		this._on_resize = this.handleResize.bind(this);
		window.addEventListener('resize', this._on_resize);
	},

	beforeDestroy() {
		this.destroyDrag();

		if ( this._on_resize ) {
			window.removeEventListener('resize', this._on_resize);
			this._on_resize = null;
		}
	},

	methods: {
		allRead() {
			this.markAllSeen(this.nav);
			this.has_unseen = false;
		},

		maybeResize(event) {
			if ( event.target !== this.$refs.header )
				return;

			this.resize(event);
		},

		changeProfile() {
			const new_id = this.$refs.profiles.value,
				new_profile = this.context.profiles[new_id];

			if ( new_profile )
				this.context.currentProfile = new_profile;
		},

		changeItem(item) {
			if ( this.$refs.page && this.$refs.page.onBeforeChange ) {
				if ( this.$refs.page.onBeforeChange(this.currentItem, item) === false )
					return;
			}

			this.markSeen(item);

			if ( item.redirect )
				return this.navigate(Array.isArray(item.redirect) ? item.redirect : item.redirect.split(/\./g));

			this.currentItem = item;
			this.restoredItem = true;

			let url;
			if ( this.exclusive ) {
				url = new URL(location.href);
				url.searchParams.set('ffz-settings', item.full_key);
				url = url.toString();
			}

			try {
				window.history.replaceState({
					...window.history.state,
					ffzcc: item.full_key
				}, document.title, url);
			} catch(err) {
				/* no-op */
			}

			let current = item;
			while(current = current.parent) // eslint-disable-line no-cond-assign
				current.expanded = true;
		},

		updateDrag() {
			if ( this.maximized )
				this.destroyDrag();
			else
				this.createDrag();
		},

		destroyDrag() {
			if ( this.displace ) {
				this.displace.destroy();
				this.displace = null;
			}
		},

		createDrag() {
			this.$nextTick(() => {
				if ( ! this.maximized )
					this.displace = displace(this.$el, {
						handle: this.$el.querySelector('header'),
						highlightInputs: true,
						constrain: true,
						onMouseDown: () => this.onFocus(),
						onTouchStart: () => this.onFocus(),
						ignoreFn(event) {
							return event.target.closest('button') != null
						}
					});
			})
		},

		onFocus() {
			if ( ! this.exclusive )
				this.z = getDialogNextZ();
		},

		handleResize() {
			if ( this.displace )
				this.displace.reinit();
		},

		navigate(...keys) {
			let item;
			for(const key of keys) {
				item = this.navKeys[key];
				if ( item )
					break;
			}

			const tabs = [];

			while(item && item.page) {
				if ( item.tab && item.parent?.tabs )
					tabs.push([item.parent, item.parent.tabs.indexOf(item)]);

				item = item.parent;
			}

			if ( ! item )
				return;

			if ( this.$refs.page && this.$refs.page.onBeforeChange ) {
				if ( this.$refs.page.onBeforeChange(this.currentItem, item) === false )
					return;
			}

			this.changeItem(item);

			// Asynchronously walk down the tab tree, so that
			// we can switch every tab correctly.
			if ( tabs.length ) {
				const bits = () => {
					const latest = tabs.pop();
					if ( latest?.[0]?._component )
						latest[0]._component.select(latest[1]);

					if ( tabs.length )
						this.$nextTick(bits);
				}

				this.$nextTick(bits);
			}

			this.$nextTick(() => {
				const el = this.$el.querySelector(`.ffz--menu-tree li[data-key="${item.full_key}"]`);
				if ( el )
					el.scrollIntoView();
			});
		}
	}
}
