import settingMixin from '../setting-mixin';
import Sortable from 'sortablejs';
import { deep_copy } from 'utilities/object';

let last_id = 0;

export default {
	mixins: [settingMixin],
	props: ['item', 'context'],

	data() {
		return {

		}
	},

	computed: {
		val() {
			if ( ! this.has_value )
				return [];

			const missing = new Set(this.data.keys());
			const out = this.value.map(id => {
				missing.delete(id);
				const data = this.data.get(id);
				return data
					? data
					: {id};
			});

			for(const key of missing) {
				const data = this.data.get(key);
				out.push(data);
			}

			return out;
		}
	},

	mounted() {
		this._sortable = Sortable.create(this.$refs.list, {
			draggable: 'section',
			filter: 'button',

			onUpdate: event => {
				if ( event.newIndex === event.oldIndex )
					return;

				const new_val = Array.from(this.val);
				new_val.splice(event.newIndex, 0, ...new_val.splice(event.oldIndex, 1));
				this.setValue(new_val);
			}
		});
	},

	beforeDestroy() {
		if ( this._sortable )
			this._sortable.destroy();

		this._sortable = null;
	},

	methods: {
		setValue(input) {
			if (input?.length > 0)
				this.set(input.map(x => x.id));
			else
				this.clear();
		},

		addPriorities() {
			this.set([...this.data.keys()]);
		}
	}

}

