import SettingMixin from '../setting-mixin';

export default {
	mixins: [SettingMixin],
	props: ['item', 'context'],

	computed: {
		nested_data() {
			const out = [];
			let current_group = null;
			let i = 0;

			for(const entry of this.data) {
				if ( entry.separator ) {
					current_group = {
						key: entry.key ?? i,
						entries: [],
						i18n_key: entry.i18n_key,
						title: entry.title,
						disabled: entry.disabled
					};

					out.push(current_group);

				} else if ( current_group != null )
					current_group.entries.push(Object.assign({v: i}, entry));
				else
					out.push(Object.assign({v: i}, entry));

				i++;
			}

			return out;
		}
	},

	methods: {
		onChange() {
			if ( this.item.multiple ) {
				const items = this.$refs.control.selectedOptions,
					out = [];

				for(let i=0; i < items.length; i++) {
					const raw = this.data[items[i].index];
					if ( raw )
						out.push(raw.value);
				}

				this.set(out)
				return;
			}

			const idx = this.$refs.control.value,
				raw_value = this.data[idx];

			if ( raw_value )
				this.set(raw_value.value);
		}
	}
}
