import {deep_copy} from 'utilities/object';

let id = 0;

export default {
	props: {
		reason: Object,
		adding: {
			type: Boolean,
			default: false
		}
	},

	data() {
		if ( this.adding )
			return {
				editor_id: id++,
				deleting: false,
				editing: true,
				edit_data: deep_copy(this.reason)
			}

		return {
			editor_id: id++,
			deleting: false,
			editing: false,
			edit_data: null
		}
	},

	computed: {
		display() {
			return this.editing ? this.edit_data : this.reason;
		},

		title() {
			if ( typeof this.display.i18n === 'string' )
				return this.t(this.display.i18n, this.display.text);

			return this.display.text;
		}
	},

	methods: {
		edit() {
			this.editing = true;
			this.edit_data = deep_copy(this.reason);
		},

		toggleRemove() {
			if ( this.editing )
				this.edit_data.remove = ! this.edit_data.remove;
		},

		cancel() {
			if ( this.adding ) {
				this.edit_data = deep_copy(this.reason);

			} else {
				this.editing = false;
				this.edit_data = null;
			}
		},

		save() {
			this.$emit('save', this.edit_data);
			this.cancel();
		}
	}
}

