export default {
	props: ['value', 'defaults'],

	data() {
		const ffz = window.FrankerFaceZ.get(),
			settings = ffz?.resolve('settings'),
			profiles = settings?.__profiles;

		return {
			profiles: profiles ? profiles.map(prof => ({
				value: prof.uuid,
				name: prof.name,
				i18n_key: prof.i18n_key
			})) : []
		}
	}
}

