import {createTester} from 'utilities/filtering';

let last_id = 0;

export default {
	props: ['value', 'type', 'filters', 'context'],

	data() {
		return {
			id: last_id++
		}
	},

	computed: {
		tester() {
			return createTester(this.value.data[0], this.filters);
		},

		passes() {
			return this.tester && this.tester(this.context)
		}
	}
}

