export default {
	props: ['context', 'item', 'value'],

	data() {
		return {
			mode: this.item.extra.mode,
			now: null
		}
	},

	mounted() {
		this.now = new Date;
		this.timer = setInterval(() => this.now = new Date, 1000);
	},

	beforeDestroy() {
		clearInterval(this.timer);
		this.timer = null;
	}

}
