import SettingMixin from '../setting-mixin';

export default {
	mixins: [SettingMixin],
	props: ['item', 'context'],

	data() {
		return {
			isCustom: false
		}
	},

	computed: {
		nested_data() {
			const out = [];
			let current_group = null;
			let i = 0;

			for(const entry of this.data) {
				if ( entry.separator ) {
					current_group = {
						key: entry.key ?? i,
						entries: [],
						i18n_key: entry.i18n_key,
						title: entry.title,
						disabled: entry.disabled
					};

					out.push(current_group);

				} else if ( current_group != null )
					current_group.entries.push(Object.assign({v: i}, entry));
				else
					out.push(Object.assign({v: i}, entry));

				i++;
			}

			return out;
		}
	},

	watch: {
		value(val) {
			for(const item of this.data)
				if ( item.value === val )
					return;

			this.isCustom = true;
		},

		has_value() {
			if ( ! this.has_value )
				this.isCustom = false;
		}
	},

	methods: {
		onChange() {
			const idx = this.$refs.control.value,
				raw_value = this.data[idx];

			if ( raw_value ) {
				this.set(raw_value.value);
				this.isCustom = false;
			} else
				this.isCustom = true;
		},

		onTextChange() {
			const value = this.$refs.text.value;
			if ( value != null )
				this.set(value);
		}
	}
}
